            <el-upload
              :action="ossInfo.host"
              :data="ossInfo"
              ref="banner"
              list-type="picture-card"
              :before-upload="beforeUpload"
              :on-success="uploadSuccess2"
              :on-remove="removeChange2"
              :limit="1"
              :file-list="bannerUrl"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="submitForm">{{isAdd?'创建':'编辑'}}视频</el-button>
