<script>
// @ is an alias to /src

export default {
  name: "addVideo",
  props: ["videodata", "isAdd"],
  data() {
    return {
      ossInfo: {
        host: "#"
      },
      fileList: [],
      coverUrl: [],
      bannerUrl: [],
      form: {
        title: "",
        desc: "",
        tag: [],
        type: "",
        author: "",
        biliurl: "",
        isrecommend: false,
        isbanner: false,
        cover: "",
        banner: "",
        proxy: [],
        startTime: ""
      },
      rules: {
        title: [{ required: true, message: "啊，忘了标题了", trigger: "blur" }],
        biliurl: [
          { required: true, message: "啊，忘了biliurl了", trigger: "blur" }
        ],
        author: [
          { required: true, message: "啊，忘了作者了", trigger: "blur" }
        ],
        desc: [{ required: true, message: "啊，忘了介绍了", trigger: "blur" }],
        tag: [{ required: true, message: "啊，忘了标签了", trigger: "change" }],
        type: [
          { required: true, message: "啊，忘了分类了", trigger: "change" }
        ],
        proxy: [
          { required: true, message: "啊，忘了代理商了", trigger: "blur" }
        ]
      }
    };
  },
  async created() {
    if (!this.isAdd) {
      this.appendForm();
    }
  },
  methods: {
    appendForm() {
      for (let k in this.form) {
        this.form[k] = this.videodata[k];
      }
      if (this.videodata.cover)
        this.coverUrl[0] = { url: this.videodata.cover };
      if (this.videodata.banner)
        this.bannerUrl[0] = { url: this.videodata.banner };
    },
    async setVideoInfo() {
      if (!this.isAdd) this.form.pid = this.videodata._id;
      let { data } = this.isAdd
        ? await axios.post("/setVideoInfo", this.form)
        : await axios.post("updateVideoInfo", this.form);
      if (data.status == 0) {
        this.$message.success(data.message);
        this.resetForm();
        this.$emit("update");
      } else {
        this.$message.error(data.message);
      }
    },
    uploadSuccess({ data }) {
      this.form.cover = data.filename;
    },
    uploadSuccess2({ data }) {
      this.form.banner = data.filename;
    },
    removeChange(file, fileList) {
      this.form.cover = "";
    },
    removeChange2(file, fileList) {
      this.form.banner = "";
    },
    async beforeUpload(file) {
      try {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error("上传封面图片大小不能超过 2MB!");
          return Promise.reject();
        }
        let { data } = await axios.get("/ossSign");
        let obj = data.data;
        obj.key = obj.dirPath + obj.key + file.name;
        this.ossInfo = data.data;
      } catch (error) {}
    },
    submitForm() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          this.setVideoInfo();
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["form"].resetFields();
    }
  }
};
</script>



