<style scoped>
.dmb-banner-link {
  height: 180px;
  background: no-repeat center;
}
.dhb-space {
  margin-top: 20px;
}
.table-box {
  padding: 0 40px;
}

.table-expand li {
  margin-bottom: 10px;
}

.table-expand .biliurl {
  border-radius: 8px;
  width: 400px;
  padding: 10px;
  font-size: 14px;
  line-height: 20px;
  background: #f1f1f1;
}
</style>

<template>
  <div>
    <div style="overflow:hidden;">
      <div
        class="dmb-banner-link"
        :style="{backgroundImage: `url(${require('../../assets/images/banner.png')})` }"
      ></div>
    </div>
    <div class="dhb-space">
    
    
      <div class="table-box">
      <div  style="text-align:right;margin-bottom:20px;">
          <el-button type="success" @click="editVideoInfo(true)">新增</el-button>
      </div>
        <el-card class="box-card" v-loading="tableLoading">
          <el-table :data="videoList" style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="props">
                <div class="table-expand">
                  <ul>
                    <li class="desc">简介：{{ props.row.desc }}</li>
                    <li>标签：
                      <el-tag
                        v-for="(tag,index) in props.row.tag"
                        style="margin-right:10px;"
                        :key="index"
                        type="danger"
                      >{{tag | tag}}</el-tag>喜欢：
                      <el-button type="success" plain size="small">
                        {{props.row.like}}
                        <i class="el-icon-star-off el-icon--right"></i>
                      </el-button>
                       播放厂商：
                       <el-tag
                        v-for="(proxy,index) in props.row.proxy"
                        style="margin-right:10px;"
                        :key="index"
                        type="danger"
                      >{{proxy }}</el-tag>
                    </li>
                    <li class="biliurl">{{ props.row.biliurl }}</li>
                  </ul>
                </div>
              </template>
            </el-table-column>
            <!-- <el-table-column label="pid" prop="_id" width="300"></el-table-column> -->
            <el-table-column label="标题" prop="title" show-overflow-tooltip min-width="300"></el-table-column>
            <el-table-column label="原作者" prop="author" show-overflow-tooltip width="120"></el-table-column>
             <el-table-column label="播放时间" prop="startTime" width="180"></el-table-column>
            <el-table-column label="创建时间" prop="createTime" width="180"></el-table-column>
            <el-table-column label="更新时间" prop="loastTime" width="180"></el-table-column>
            <el-table-column label="封面图" prop="cover" width="80">
              <template slot-scope="scope">
                <img :src="scope.row.cover" width="40" height="40" alt>
              </template>
            </el-table-column>
            <el-table-column label="banner图" prop="banner" width="100">
              <template slot-scope="scope">
                <img :src="scope.row.banner" width="40" height="40" alt>
              </template>
            </el-table-column>
            <el-table-column label="分类" prop="type" width="120">
              <template slot-scope="scope">
                <el-tag type="success">{{scope.row.type | type}}</el-tag>
              </template>
            </el-table-column>
            <!-- <el-table-column label="更新版本" prop="__v" width="80"></el-table-column> -->
            <!-- <el-table-column label="当前状态" prop="status" width="80"></el-table-column> -->
            <el-table-column label="banner" prop="isbanner" width="80">
              <template slot-scope="scope">
                <el-switch
                  v-model="videoList[scope.$index].isbanner"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  @change="updateVideoInfo(scope.row._id,{isbanner:videoList[scope.$index].isbanner})"
                ></el-switch>
              </template>
            </el-table-column>
            <el-table-column label="首推" prop="isrecommend" width="80">
              <template slot-scope="scope">
                <el-switch
                  v-model="videoList[scope.$index].isrecommend"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  @change="updateVideoInfo(scope.row._id,{isrecommend:videoList[scope.$index].isrecommend})"
                ></el-switch>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="150">
              <template slot-scope="scope">
                <el-button size="mini" type="warning" @click="editVideoInfo(false,scope.$index)">编辑</el-button>
                <el-button size="mini" type="danger" @click="removeVideoInfo(scope.row._id)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>

          <div style="margin-top:20px; text-align:right;">
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="pageCurrent"
              :page-sizes="[10, 20,50,100]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </el-card>
      </div>
    </div>
    <!-- 编辑 -->
    <el-dialog
      :title="isAdd?'新增':'编辑'"
      v-if="dialogVisible"
      custom-class="dialog"
      :visible.sync="dialogVisible"
    >
      <addVideo :videodata="videoData" :isAdd="isAdd" @update="getVideoList"></addVideo>
    </el-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import addVideo from "./addVideo";
export default {
  name: "space",
  components: {
    addVideo
  },
  data() {
    return {
      isAdd: false,
      videoData: null,
      dialogVisible: false,
      tableLoading: false,
      pageSize: 10,
      pageCurrent: 1,
      total: 0,
      videoList: null
    };
  },
 async created() {
    this.getVideoList();
  },
  methods: {
    /**
     * @description: 编辑的时候给组件传的值
     * @param {type} 
     * @return: 
     */
    editVideoInfo(isAdd, index) {
      this.dialogVisible = true;
      this.isAdd = isAdd;
      this.videoData = this.videoList[index];
    },
    /**
     * @description: 删除话不多说
     * @param {type} 
     * @return: 
     */
    async removeVideoInfo(pid) {
      try {
        await this.$confirm("删除嘛，嘿嘿嘿?", "我删", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        });
        this.tableLoading = true;
        let { data } = await axios.post("removeVideoInfo", {
          pid
        });
        if (data.status == 0) {
          this.$message.success(data.message);
          this.getVideoList();
        } else {
          this.$message.error("删除失败");
        }
        this.tableLoading = false;
      } catch (err) {
        this.$message.error("删除失败");
        this.tableLoading = false;
      }
    },
    /**
     * @description: 更新你懂的
     * @param {type} 
     * @return: 
     */
    async updateVideoInfo(pid, info) {
      try {
        this.tableLoading = true;
        let { data } = await axios.post("updateVideoInfo", {
          pid,
          ...info
        });
        this.tableLoading = false;
        if (data.status == 0) {
          this.$message.success(data.message);
          this.getVideoList();
        } else {
          this.$message.error("修改失败");
        }
      } catch (error) {
        this.tableLoading = false;
      }
    },
    /**
     * @description: 查询你懂的
     * @param {type} 
     * @return: 
     */
    async getVideoList() {
      this.dialogVisible = false;
      try {
        this.tableLoading = true;
        let { data } = await axios.get("getVideoList", {
          params: {
            pageSize: this.pageSize,
            pageCurrent: this.pageCurrent - 1
          }
        });
        this.tableLoading = false;
        if (data.status == 0) {
          this.total = data.data.total;
          this.videoList = data.data.list;
        }
      } catch (error) {
        this.tableLoading = false;
      }
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getVideoList();
    },
    handleCurrentChange(val) {
      this.pageCurrent = val;
      this.getVideoList();
    }
  }
};
</script>



